<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
transition-group(
  :appear="true"
  :class=`[
    "c-instagram-profile",
    "c-instagram-profile--" + position,
    "c-instagram-profile--" + theme,
    "u-no-drag",
    "u-reset-widget",
    {
      "c-instagram-profile--editor": editor
    }
  ]`
  enter-active-class="u-animated u-ms500 u-fade-in-left"
  leave-active-class="u-animated u-ms250 u-fade-out-left"
  tag="div"
)
  widget-close(
    v-if="triggered && closed === false"
    @close="onClose"
    class="c-instagram-profile__close"
    key="close"
    theme="light"
  )

  div(
    v-if="triggered && closed === false"
    class="c-instagram-profile__widget"
    key="widget"
  )
    .c-instagram-profile__header
      img(
        @click="onGoToInstagram"
        :src="data.picture"
        class="c-instagram-profile__picture"
      )

      .c-instagram-profile__information
        .c-instagram-profile__account
          span(
            @click="onGoToInstagram"
            class="c-instagram-profile__name"
          ) {{ data.name }}

          span(
            v-if="showVerifiedBadge"
            class="c-instagram-profile__verified"
          )

        p(
          v-if="showBiography && data.biography"
          v-html="formattedBiography"
          class="c-instagram-profile__biography"
        )

        instagram-follow(
          @click="onGoToInstagram"
          class="c-instagram-profile__follow u-animated u-zoom-in u-ms250 u-delay250"
          size="small"
        ) Follow us on Instagram

    div(
      v-if="showStatistics"
      class="c-instagram-profile__statistics"
    )
      instagram-statistic(
        v-for="(statistic, type) in data.statistics"
        @click="onGoToInstagram"
        :abbreviate="statistic.abbreviate"
        :label="statistic.label"
        :separator="statistic.separator"
        :key="type"
        :theme="theme"
        :value="statistic.value"
      )

    instagram-stories(
      v-if="showStories && data.stories.length > 0"
      :stories="data.stories"
      :theme="theme"
      class="c-instagram-profile__stories"
    )

    instagram-medias(
      v-if="showMedias && data.medias.length > 0"
      :medias="data.medias"
      :theme="theme"
      class="c-instagram-profile__medias"
    )

    widget-branding(
      v-if="!premium"
      :theme="theme"
      class="c-instagram-profile__branding"
    )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// TODO: SIZE
// TODO: LINK WITH INSTAGRAM API WHEN ACCEPTED
// TODO: ABILITY TO CONTROL MOST THINGS
// TODO: RELEASE ON PRODUCTION

// NPM
import axios from "axios"

// PROJECT: COMPONENTS
import InstagramFollow from "@/components/instagram/InstagramFollow"
import InstagramMedias from "@/components/instagram/InstagramMedias"
import InstagramStatistic from "@/components/instagram/InstagramStatistic"
import InstagramStories from "@/components/instagram/InstagramStories"
import WidgetBranding from "@/components/widgets/WidgetBranding"
import WidgetClose from "@/components/widgets/WidgetClose"

// PROJECT: FILTERS
import { truncate } from "@/filters/text"

// PROJECT: MIXINS
import CloseMixin from "@/mixins/CloseMixin"
import PositionMixin from "@/mixins/PositionMixin"
import ThemeMixin from "@/mixins/ThemeMixin"
import TriggerMixin from "@/mixins/TriggerMixin"
import WidgetMixin from "@/mixins/WidgetMixin"

export default {
  name: "InstagramProfile",

  components: {
    InstagramFollow,
    InstagramMedias,
    InstagramStatistic,
    InstagramStories,
    WidgetBranding,
    WidgetClose
  },

  mixins: [CloseMixin, PositionMixin, ThemeMixin, TriggerMixin, WidgetMixin],

  props: {
    accessToken: {
      type: String,
      required: true
    },
    instagramId: {
      type: String,
      required: true
    },
    showBiography: {
      type: Boolean,
      default: true
    },
    showMedias: {
      type: Boolean,
      default: true
    },
    showStatistics: {
      type: Boolean,
      default: false
    },
    showStories: {
      type: Boolean,
      default: false
    },
    showVerifiedBadge: {
      type: Boolean,
      default: true
    }
  },

  data: () => ({
    // --> STATE <--

    data: {
      username: "",
      name: "",
      biography: "",
      picture: "",

      statistics: {
        medias: {
          label: "Posts",
          value: 0,
          separator: true
        },
        followers: {
          label: "Followers",
          value: 0,
          abbreviate: true
        },
        follows: {
          label: "Following",
          value: 0
        }
      },

      stories: [],

      medias: []
    },

    endpoints: {
      user:
        "https://graph.facebook.com/v3.2/{instagramId}?fields={fields}&access_token={accessToken}",
      userMedias:
        "https://graph.facebook.com/v3.2/{instagramId}/media?access_token={accessToken}",
      userStories:
        "https://graph.facebook.com/v3.2/{instagramId}/stories?access_token={accessToken}"
    }
  }),

  computed: {
    formattedBiography() {
      // Wrap each hashtag with a link
      let biography = truncate(this.data.biography, 55)

      biography = biography.replace(
        /(^|\W)(#[a-z\d][\w-]*)/gi,
        '$1<a class="c-instagram-profile__hashtag" href="https://www.instagram.com/explore/tags/$2/" target="_blank">$2</a>'
      )

      // Replace # character in hrefs
      biography = biography.replace(/\/tags\/#/g, "/tags/")

      return biography
    }
  },

  created() {
    this.fetchProfile()

    if (this.showMedias) {
      this.fetchMedias()
    }

    if (this.showStories) {
      this.fetchStories()
    }
  },

  methods: {
    // --> HELPERS <--

    async fetchProfile() {
      try {
        const { data } = await axios.get(
          this.endpoints.user
            .replace("{instagramId}", this.instagramId)
            .replace("{accessToken}", this.accessToken)
            .replace(
              "{fields}",
              "username,name,biography,profile_picture_url,media_count,followers_count,follows_count"
            )
        )

        this.data.username = data.username
        this.data.name = data.name
        this.data.biography = data.biography
        this.data.picture = data.profile_picture_url
        this.data.statistics.medias.value = data.media_count
        this.data.statistics.followers.value = data.followers_count
        this.data.statistics.follows.value = data.follows_count
      } catch (error) {
        console.error(error)
      }
    },

    async fetchMedias() {
      try {
        const { data } = await axios.get(
          this.endpoints.userMedias
            .replace("{instagramId}", this.instagramId)
            .replace("{accessToken}", this.accessToken)
        )
        this.data.medias = data
      } catch (error) {
        console.error(error)
      }
    },

    async fetchStories() {
      try {
        const { data } = await axios.get(
          this.endpoints.userStories
            .replace("{instagramId}", this.instagramId)
            .replace("{accessToken}", this.accessToken)
        )
        this.data.stories = data
      } catch (error) {
        console.error(error)
      }
    },

    // --> EVENT LISTENERS <--

    onGoToInstagram() {
      window.open(`https://www.instagram.com/${this.data.username}`, "_blank")
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
