<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
div(
  :class=`[
    "c-instagram-medias",
    "c-instagram-medias--" + theme
  ]`
)
  div(
    v-for="media in medias"
    @click="onMediaClick(media.permalink)"
    :key="media.id"
    class="c-instagram-medias__media u-animated u-zoom-in u-ms250 u-delay250"
  )
    img(
      :src="media.media_type.toLowerCase() === 'video' ? media.thumbnail_url : media.media_url"
      class="c-instagram-medias__image"
    )

    span(
      v-if="media.media_type && ['carousel_album', 'video'].includes(media.media_type.toLowerCase())"
      :class=`[
        "c-instagram-medias__icon",
        "c-instagram-medias__icon--" + media.media_type.toLowerCase()
      ]`
    )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: MIXINS
import ThemeMixin from "@/mixins/ThemeMixin"

export default {
  mixins: [ThemeMixin],

  props: {
    medias: {
      type: Array,
      default: () => []
    }
  },

  methods: {
    // --> EVENT LISTENERS <--

    onMediaClick(permalink) {
      window.open(permalink, "_blank")
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-instagram-medias";

#{$c} {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: repeat(auto-fill, 60px);
  justify-content: center;
  padding: 10px;
  user-select: none;

  #{$c}__media {
    position: relative;
    display: none;
    overflow: hidden;
    width: 60px;
    height: 60px;
    border-radius: 5px;
    cursor: pointer;

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3) {
      display: initial;
    }

    @for $i from 1 through 8 {
      &:nth-child(#{$i}) {
        animation-delay: 75ms * $i;
      }
    }

    #{$c}__image {
      display: inline-block;
      width: 100%;
      height: 100%;
    }

    #{$c}__icon {
      position: absolute;
      top: -1px;
      right: 0;
      display: inline-block;
      width: 24px;
      height: 24px;
      background-size: cover;

      &--carousel_album {
        background-image: url(data:image/png;base64,);
      }

      &--video {
        background-image: url(data:image/png;base64,);
      }
    }

    &:hover {
      #{$c}__image {
        filter: brightness(1.1);
      }
    }
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      #{$c}__media {
        @if ($themeName == "dark") {
          box-shadow: 0 1px 5px mdg($theme, "shadows", "default", "primary");
        }
      }
    }
  }
}

// --> BREAKPOINTS <--

@include mq($from: 297px) {
  #{$c}__media {
    &:nth-child(4) {
      display: initial;
    }
  }
}

@include mq($from: 362px) {
  #{$c}__media {
    &:nth-child(5) {
      display: initial;
    }
  }
}

@include mq($from: mobileWide) {
  #{$c} {
    #{$c}__media {
      display: initial;
    }
  }
}
</style>
